import { ensureAllElements, ensureElement } from "../../utils/utils";
import { Form } from "../common/Form";
export class PaymentFormUI extends Form {
    constructor(container, events) {
        super(container, events);
        this.events = events;
        this._div = ensureElement('.order__buttons', container);
        this._buttons = ensureAllElements('.button_alt', container);
        this._div.addEventListener('click', (evt) => {
            const target = evt.target;
            this.setClassMethodSelected(target.name);
            events.emit('paymentMethod:change', { target: target.name });
        });
    }
    setClassMethodSelected(name) {
        this._buttons.forEach(button => {
            if (button.name === name) {
                this.toggleClass(button, 'button_alt-active', true);
            }
            else {
                this.toggleClass(button, 'button_alt-active', false);
            }
        });
    }
    set paymentMethod(method) {
        this.setClassMethodSelected(method);
    }
    set address(address) {
        this.container.elements.namedItem('address').value = address;
    }
}
