import { Model } from "../base/model";
export class App extends Model {
    constructor(data, events) {
        super(data, events);
        this.events = events;
        this.catalog = [];
        this.basket = [];
    }
    set catalog(catalog) {
        this._catalog = catalog;
    }
    get catalog() {
        return this._catalog;
    }
    set basket(basket) {
        this._basket = basket;
    }
    get basket() {
        return this._basket;
    }
    get order() {
        return this._order;
    }
    setCatalog(items) {
        this.catalog = items;
        this.emitChanges('items:chenges', { catalog: this.catalog });
    }
    getPrice() {
        return this._basket.reduce((acc, item) => {
            return acc += Number(item.price);
        }, 0);
    }
    initOrder(order) {
        this._order = order;
        this._order.clearOrder();
        return this.order;
    }
    clearBasket() {
        this.basket = [];
    }
    addInBasket(cardId) {
        const card = this.catalog.find(card => card.id === cardId);
        this._basket.push(card);
    }
    removeFromBasket(cardId) {
        this._basket = this.basket.filter(card => card.id !== cardId);
    }
    getItemsIdFromBasket() {
        return this.basket.map(item => item.id);
    }
    createOrderPostData() {
        return {
            payment: this._order.paymentMethod,
            address: this._order.address,
            email: this._order.email,
            phone: this._order.phone,
            total: this.getPrice(),
            items: this.getItemsIdFromBasket()
        };
    }
}
