import { ensureElement } from "../../utils/utils";
import { UIComponent } from "../base/view";
export class SuccesUI extends UIComponent {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._button = ensureElement('.order-success__close', container);
        this._message = ensureElement('.order-success__description', container);
        this._button.addEventListener('click', () => {
            this.events.emit('succes:close');
        });
    }
    set message(totalPrice) {
        this.setTextContent(this._message, `Списано ${totalPrice} синапсов`);
    }
}
