export class UIComponent {
    constructor(container) {
        this.container = container;
    }
    setTextContent(element, counter) {
        element.textContent = counter;
    }
    setDisabled(element, state) {
        state ? element.setAttribute('disabled', 'disabled') : element.removeAttribute('disabled');
    }
    toggleClass(element, className, flag) {
        element.classList.toggle(className, flag);
    }
    render(data) {
        Object.assign(this, data !== null && data !== void 0 ? data : {});
        return this.container;
    }
}
