import { createElement, ensureElement, ensureAllElements } from "../../utils/utils";
import { UIComponent } from "../base/view";
export class BasketUI extends UIComponent {
    constructor(container, events) {
        super(container);
        this._list = ensureElement('.basket__list', container);
        this._price = ensureElement('.basket__price', container);
        this._button = ensureElement('.basket__button', container);
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit("payment:open");
            });
        }
    }
    set price(price) {
        this.setTextContent(this._price, `${price} синапсов`);
    }
    set valid(value) {
        this.setDisabled(this._button, !value);
    }
    set list(li) {
        if (li.length) {
            this._list.replaceChildren(...li);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
        }
    }
    renderWithIndex(data) {
        this._index = ensureAllElements('.basket__item-index', this.container);
        this._index.forEach((item, index) => {
            this.setTextContent(item, String(index + 1));
        });
        return super.render(data);
    }
}
