import { Model } from '../../components/base/model';
var validate = require("validate.js");
export class Order extends Model {
    constructor() {
        super(...arguments);
        this._paymentMethod = 'online';
        this._address = '';
        this._email = '';
        this._phone = '';
        this._items = [];
        this._errors = {
            paymentMethodError: 'Выберете способ оплаты',
            addressError: 'Введите адрес доставки',
            phoneError: 'Введите телефон',
            emailError: 'Введите почту'
        };
    }
    set paymentMethod(method) {
        this._paymentMethod = method;
        this.validatePaymentMethod();
    }
    get paymentMethod() {
        return this._paymentMethod;
    }
    set address(address) {
        this._address = address;
        this.validateAddress();
    }
    get address() {
        return this._address;
    }
    set email(email) {
        this._email = email;
        this.validateMail();
    }
    get email() {
        return this._email;
    }
    set phone(phone) {
        this._phone = phone;
        this.validatePhone();
    }
    get phone() {
        return this._phone;
    }
    set items(items) {
        this._items = items;
    }
    get items() {
        return this._items;
    }
    validatePaymentMethod() {
        if (!this._paymentMethod) {
            this._errors.paymentMethodError = "Выберете способ оплаты";
        }
        else {
            this._errors.paymentMethodError = '';
        }
        this.emitChanges('errors:changed', this._errors);
    }
    validateAddress() {
        if (!this._address) {
            this._errors.addressError = "Введите адрес доставки";
        }
        else {
            this._errors.addressError = '';
        }
        this.emitChanges('errors:changed', this._errors);
    }
    validateMail() {
        if (!this.email) {
            this._errors.emailError = "Введите почту";
        }
        else {
            this._errors.emailError = '';
        }
        this.emitChanges('errors:changed', this._errors);
    }
    validatePhone() {
        if (!this._phone) {
            this._errors.phoneError = "Введите телефон";
        }
        else {
            this._errors.phoneError = '';
        }
        this.emitChanges('errors:changed', this._errors);
    }
    clearOrder() {
        this._paymentMethod = 'online';
        this._address = '';
        this._email = '';
        this._phone = '';
    }
    applyOrder() {
        this.clearOrder();
        this.emitChanges('order:apply');
    }
}
