import { Form } from "../common/Form";
export class ContactFormUI extends Form {
    constructor(container, events) {
        super(container, events);
        this.events = events;
    }
    set email(email) {
        this.container.elements.namedItem('email').value = email;
    }
    set phone(phone) {
        this.container.elements.namedItem('phone').value = phone;
    }
}
